import React from 'react';
import {Link} from 'react-router';

export default class StatusButtons extends React.Component {
  static propTypes = {
    createBarcodeHandler: React.PropTypes.func,
    showHistoryHandler: React.PropTypes.func,
    latestLifelog: React.PropTypes.object
  }

  static defaultProps = {
    latestLifelog: {}
  }

  render() {
    return (
      <div className='buttons row'>
        <Link to='/print' id='barcodeBtn' className='btn btn-lg btn-block btn-default'
          query={{
            steps: this.props.latestLifelog.steps,
            distance_m: this.props.latestLifelog.distanceM,
            usage_time_s: this.props.latestLifelog.usageTimeS
          }}>
          
        </Link>
        <Link to='/history' id='historyBtn' className='btn btn-lg btn-block btn-default'>
          
        </Link>
      </div>
    );
  }
}



