import React       from 'react';

export default class HistoryItem extends React.Component {
  static propTypes = {
    characterName: React.PropTypes.string,
    cardImgUrl: React.PropTypes.string,
    characterHp: React.PropTypes.number,
    characterType: React.PropTypes.string
  }

  render () {
    return (
      <div className='well'>
        <div className='card-title row'>
          <img src="../img/logo-card.png" alt="LIFE CORD BATTLER" className="col-xs-8" />
        </div>
        <img src={this.props.cardImgUrl} alt="" className="card-img" />
        <div className='card-info'>
          <h3 className="card-character">{this.props.characterName}</h3>
          <p>{this.props.characterType}</p>
          <p>HP {this.props.characterHp}</p>
        </div>
      </div>
    );
  }
}



