import React from 'react';

export default class Barcode extends React.Component {
  static propTypes = {
    barcodeImgUrl: React.PropTypes.string,
    characterLv: React.PropTypes.number
  }

  renderCardImage() {
    let src = '';
    switch (this.props.characterLv) {
    case 1:
      src = '../img/character-lv1.png';
      break;
    case 2:
      src = '../img/character-lv2.png';
      break;
    case 3:
      src = '../img/character-lv3.png';
      break;
    case 4:
      src = '../img/character-lv4.png';
      break;
    default:
      src = '../img/character-lv5.png';
      break;
    }

    return <img src={src} alt="" className="card-img" />;
  }

  render() {
    return (
      <div className='well'>
        <div className='card-title row'>
          <img src="../img/logo-card.png" alt="LIFE CORD BATTLER" className="col-xs-8" />
        </div>
        <div className='trimming'>
          { this.renderCardImage() }
        </div>
        <div className='card-barcode row'>
          <img src={this.props.barcodeImgUrl} alt="" className="col-xs-offset-2 col-xs-8" />
        </div>
      </div>
    );
  }
}



