import { Route }   from 'react-router';
import React       from 'react';
import CoreLayout  from 'layouts/CoreLayout';
import HomeView    from 'views/HomeView';
import PrintView from 'views/PrintView';
import HistoryView from 'views/HistoryView';

export default (
  <Route key='root' component={CoreLayout}>
    <Route key='home' name='home' path='/home' component={HomeView} />
    <Route key='print' name='print' path='/print' component={PrintView} />
    <Route key='history' name='history' path='/history' component={HistoryView} />
  </Route>
);



