import React       from 'react';
import { connect } from 'react-redux';
import {loadBarcodesToday} from '../actions';
import BarcodeWidget from '../components/BarcodeWidget';

const mapStateToProps = (state) => {
  const {
    router: {
      location: { query }
    },
    entities: { barcodesToday }
  } = state;

  return {
    barcodes: barcodesToday[undefined], // TODO: id
    userId: query.id,
    steps: query.steps,
    distanceM: query.distanceM,
    usageTimeS: query.usage_time_s
  };
};

export class PrintView extends React.Component {
  static propTypes = {
    dispatch : React.PropTypes.func,
    counter  : React.PropTypes.number,
    barcodes: React.PropTypes.array,
    loadBarcodesToday: React.PropTypes.object,
    userId: React.PropTypes.number,
    steps: React.PropTypes.number,
    distanceM: React.PropTypes.number,
    usageTimeS: React.PropTypes.number
  }

  constructor () {
    super();
  }

  componentWillMount() {
    this.props.loadBarcodesToday(
      this.props.userId,
      this.props.steps,
      this.props.distanceM,
      this.props.usageTimeS
      );
  }

  render () {
    if (!this.props.barcodes) {
      return <div className="text-center" style={{marginTop: '2em'}}><i className="fa fa-spinner fa-spin fa-5x"></i></div>;
    }

    return (
      <div id='lifecode-print' className='container'>
        <h2>TODAY'S CARDS</h2>
        <BarcodeWidget barcodes={this.props.barcodes} />
      </div>
    );
  }
}

export default connect(mapStateToProps, {loadBarcodesToday})(PrintView);



