import React       from 'react';
import { connect } from 'react-redux';
import {loadLatestLifelog} from '../actions';
import StatusWidget from '../components/StatusWidget';
import StatusButtons from '../components/StatusButtons';

// We define mapStateToProps where we'd normally use the @connect
// decorator so the data requirements are clear upfront, but then
// export the decorated component after the main class definition so
// the component can be tested w/ and w/o being connected.
// See: http://rackt.github.io/redux/docs/recipes/WritingTests.html
function mapStateToProps(state) {
  // const { login } = state.router.params;
  const {
    // pagination: { starredByUser },
    router: {
      location: { query }
    },
    entities: { latestLifelogs }
  } = state;

  return {
    latestLifelog: latestLifelogs[undefined], // TODO: id
    userId: query.id
  };
}

export class HomeView extends React.Component {
  static propTypes = {
    dispatch : React.PropTypes.func,
    loadLatestLifelog  : React.PropTypes.func,
    latestLifelog: React.PropTypes.object,
    userId: React.PropTypes.string
  }

  constructor () {
    super();
  }

  componentWillMount() {
    this.props.loadLatestLifelog(this.props.userId);
  }

  render () {
    if (!this.props.latestLifelog) {
      return <div className="text-center" style={{marginTop: '2em'}}><i className="fa fa-spinner fa-spin fa-5x"></i></div>;
    }
    return (
      <div id='lifecode-home' className='container'>
        <StatusWidget {...this.props.latestLifelog} />
        <StatusButtons latestLifelog={this.props.latestLifelog} />
      </div>
    );
  }
}

export default connect(mapStateToProps, {loadLatestLifelog})(HomeView);



