import React       from 'react';
import geolib from 'geolib';
import Status from './Status';

export default class StatusWidget extends React.Component {
  static propTypes = {
    steps: React.PropTypes.number,
    distanceM: React.PropTypes.number
  }

  render () {
    return (
        <div className='lifelogs row'>
          <Status circleClassName='steps-circle' value={this.props.steps}  />
          <Status circleClassName='distance-circle' value={geolib.convertUnit('km', this.props.distanceM, 2)}  />
        </div>
      );
  }
}



