import React       from 'react';
import Barcode from './Barcode';

export default class BarcodeWidget extends React.Component {
  static propTypes = {
    barcodes: React.PropTypes.array,
    level: React.PropTypes.number
  }

  render () {
    return (
      <div className='cards'>
        <Barcode characterLv={this.props.level} barcodeImgUrl={this.props.barcodes[0].imageUrl} />
      </div>
    );
  }
}



