import React from 'react';

export default class Status extends React.Component {
  static propTypes = {
    name: React.PropTypes.string,
    value: React.PropTypes.number,
    isLoading: React.PropTypes.bool,
    circleClassName: React.PropTypes.string
  }

  render() {
    return (
      <div className='col-xs-6'>
        <div className={'lifelog-circle ' + this.props.circleClassName}>
          <span className='lifelog-count'>{this.props.value.toLocaleString()}</span>
        </div>
      </div>
    );
  }
}



